import React from "react";
import { useContext } from "react";

//  
import { dates } from "../../../data/time";

// Provider 
import { CurrentWeatherContext } from "../../../App";

const ForecastWeather = () => {
  const { forecastWeather } = useContext(CurrentWeatherContext);

  return (
    <div className="forecastWeather">
      <h2> </h2>
      <ul>
        <li>
          <div>
            <p>{dates + 1} </p>
            <img
              src={
                process.env.PUBLIC_URL +
                `/image/weather${
                  forecastWeather && forecastWeather[0].weather[0].icon
                }.svg`
              }
              alt=" "
            />
          </div>
          <p>
               {forecastWeather && forecastWeather[0].main.temp}
            ,  {forecastWeather && forecastWeather[0].main.humidity}%,
             {forecastWeather && forecastWeather[0].wind.speed}ms 
             .
          </p>
          <b>{forecastWeather && forecastWeather[0].weather[0].main}</b>
        </li>
        <li>
          <div>
            <p>{dates + 2} </p>
            <img
              src={
                process.env.PUBLIC_URL +
                `/image/weather${
                  forecastWeather && forecastWeather[1].weather[0].icon
                }.svg`
              }
              alt=" "
            />
          </div>
          <p>
               {forecastWeather && forecastWeather[1].main.temp}
            ,  {forecastWeather && forecastWeather[1].main.humidity}%,
             {forecastWeather && forecastWeather[1].wind.speed}ms 
             .
          </p>
          <b>{forecastWeather && forecastWeather[1].weather[0].main}</b>
        </li>
        <li>
          <div>
            <p>{dates + 3} </p>
            <img
              src={
                process.env.PUBLIC_URL +
                `/image/weather${
                  forecastWeather && forecastWeather[2].weather[0].icon
                }.svg`
              }
              alt=" "
            />
          </div>
          <p>
               {forecastWeather && forecastWeather[2].main.temp}
            ,  {forecastWeather && forecastWeather[2].main.humidity}%,
             {forecastWeather && forecastWeather[2].wind.speed}ms 
             .
          </p>
          <b>{forecastWeather && forecastWeather[2].weather[0].main}</b>
        </li>
        <li>
          <div>
            <p>{dates + 4} </p>
            <img
              src={
                process.env.PUBLIC_URL +
                `/image/weather${
                  forecastWeather && forecastWeather[3].weather[0].icon
                }.svg`
              }
              alt=" "
            />
          </div>
          <p>
               {forecastWeather && forecastWeather[3].main.temp}
            ,  {forecastWeather && forecastWeather[3].main.humidity}%,
             {forecastWeather && forecastWeather[3].wind.speed}ms 
             .
          </p>
          <b>{forecastWeather && forecastWeather[3].weather[0].main}</b>
        </li>
        <li>
          <div>
            <p>{dates + 5} </p>
            <img
              src={
                process.env.PUBLIC_URL +
                `/image/weather${
                  forecastWeather && forecastWeather[4].weather[0].icon
                }.svg`
              }
              alt=" "
            />
          </div>
          <p>
               {forecastWeather && forecastWeather[4].main.temp}
            ,  {forecastWeather && forecastWeather[4].main.humidity}%,
             {forecastWeather && forecastWeather[4].wind.speed}ms 
             .
          </p>
          <b>{forecastWeather && forecastWeather[4].weather[0].main}</b>
        </li>
      </ul>
    </div>
  );
};

export default ForecastWeather;
