import React from "react";
import { useContext, useState } from "react";

// Provider 
import { CurrentWeatherContext } from "../../../App";

const SearchBox = () => {
  //   
  const [searchCity, setSearchCity] = useState("");

  // Provider 
  const { todayWeather, setCity } = useContext(CurrentWeatherContext);

  const citySubmitHandler = (e) => {
    e.preventDefault();

    setCity(searchCity)
    setSearchCity("");
  }

  return (
    <div className="searchBox">
      <h2> {todayWeather && todayWeather.name} ?</h2>
      <form onSubmit={citySubmitHandler}>
        <input onChange={(e) => setSearchCity(e.target.value)} value={searchCity} type="search" placeholder="  "  />
      </form>
      <div className="keyword">
        KeyWord
        <ul>
          <li>Paris</li>
          <li>New York</li>
          <li>Japan</li>
        </ul>
      </div>
    </div>
  );
};

export default SearchBox;
