import React from "react";
import { useContext } from "react";

// 
import TemperatureIcon from "../../../assets/image/temperatureIcon.svg"
import HumidityIcon from "../../../assets/image/humidityIcon.svg"
import WindIcon from "../../../assets/image/windIcon.svg"

// Provider 
import { CurrentWeatherContext } from "../../../App";

const SearchInfo = () => {
  // Provider 
  const { todayWeather } = useContext(CurrentWeatherContext);

  return (
    <ul className="searchInfo">
      <li>
        <div>
          <h3>{todayWeather && todayWeather.name}</h3>
          <p>  {todayWeather && todayWeather.main.temp}C</p>
        </div>
        <div>
          <img src={TemperatureIcon} alt="  " />
        </div>
      </li>
      <li>
        <div>
          <h3>{todayWeather && todayWeather.name}</h3>
          <p>  {todayWeather && todayWeather.main.humidity}%</p>
        </div>
        <div>
          <img src={HumidityIcon} alt="  " />
        </div>
      </li>
      <li>
        <div>
          <h3>{todayWeather && todayWeather.name}</h3>
          <p>  {todayWeather && todayWeather.wind.deg}m/s</p>
        </div>
        <div>
          <img src={WindIcon} alt="  " />
        </div>
      </li>
    </ul>
  );
};

export default SearchInfo;
